module.exports = "<!--\r\n1. v-if\r\n1.1 在 <template> 元素上使用 v-if 条件渲染分组\r\n<template v-if=\"ok\">\r\n  <h1>Title</h1>\r\n  <p>Paragraph 1</p>\r\n  <p>Paragraph 2</p>\r\n</template>\r\n\r\n1.2 v-else v-else-if(2.1.0新增)\r\n\r\n<div v-if=\"type === 'A'\">\r\n  A\r\n</div>\r\n<div v-else-if=\"type === 'B'\">\r\n  B\r\n</div>\r\n<div v-else-if=\"type === 'C'\">\r\n  C\r\n</div>\r\n<div v-else>\r\n  Not A/B/C\r\n</div>\r\n\r\n1.3 用 key管理可复用的元素\r\n\r\n<template v-if=\"loginType === 'username'\">\r\n  <label>Username</label>\r\n  <input placeholder=\"Enter your username\">\r\n</template>\r\n<template v-else>\r\n  <label>Email</label>\r\n  <input placeholder=\"Enter your email address\">\r\n</template>\r\n那么在上面的代码中切换 loginType 将不会清除用户已经输入的内容。因为两个模板使用了相同的元素，<input> 不会被替换掉——仅仅是替换了它的 placeholder。\r\n\r\n<template v-if=\"loginType === 'username'\">\r\n  <label>Username</label>\r\n  <input placeholder=\"Enter your username\" key=\"username-input\">\r\n</template>\r\n<template v-else>\r\n  <label>Email</label>\r\n  <input placeholder=\"Enter your email address\" key=\"email-input\">\r\n</template>\r\n现在，每次切换时，输入框都将被重新渲染。\r\n\r\n2. v-show\r\n无论条件为何都会渲染dom,只是简单切换disploy。\r\nv-if 是惰性的，每次切换，都要重新渲染。\r\n\r\n3. v-if 与 v-for 一起使用\r\n当 v-if 与 v-for 一起使用时，v-for 具有比 v-if 更高的优先级\r\n-->\r\n\r\n\r\n<template>\r\n    <div>\r\n        <div class=\"case-item\">\r\n            <h4>1.1 在 template 元素上使用 v-if 条件渲染分组</h4>\r\n            <template v-if=\"ok\">\r\n                <h1>Title</h1>\r\n                <p>Paragraph 1</p>\r\n                <p>请用f12查看渲染的dom结构</p>\r\n            </template>\r\n\r\n            <h4>1.2v-else v-else-if(2.1.0新增)</h4>\r\n            <div v-if=\"type === 'A'\">\r\n                A\r\n            </div>\r\n            <div v-else-if=\"type === 'B'\">\r\n                B\r\n            </div>\r\n            <div v-else-if=\"type === 'C'\">\r\n                C\r\n            </div>\r\n            <div v-else>\r\n                Not A/B/C\r\n            </div>\r\n\r\n            <h4>1.3 用 key管理可复用的元素</h4>\r\n            <template v-if=\"loginType === 'username'\">\r\n                <label>Username</label>\r\n                <el-input placeholder=\"Enter your username\"/>\r\n            </template>\r\n            <template v-else>\r\n                <label>Email</label>\r\n                <el-input placeholder=\"Enter your email address\"/>\r\n            </template>\r\n\r\n            <template v-if=\"loginType === 'username'\">\r\n                <label>Username</label>\r\n                <el-input placeholder=\"Enter your username\" key=\"username-input\"/>\r\n            </template>\r\n            <template v-else>\r\n                <label>Email</label>\r\n                <el-input placeholder=\"Enter your email address\" key=\"email-input\"/>\r\n            </template>\r\n            <el-button type=\"primary\" @click=\"changeLoginType\">Toggle Login type</el-button>\r\n\r\n            <h4>2 v-show</h4>\r\n            <div>请用f12查看dom结构差异</div>\r\n            <div v-show=\"isShow\">v-show</div>\r\n            <div v-if=\"isShow\">v-if</div>\r\n            <el-button type=\"primary\" @click=\"switchShowState\">isShow</el-button>\r\n\r\n            <h4>3. v-if 与 v-for 一起使用</h4>\r\n            <div v-for=\"item in list\" v-if=\"item<3\">\r\n                <div> {{item}}</div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component, Watch} from 'vue-property-decorator'\r\n\r\n    @Component\r\n    export default class BindingHTMLClasses extends Vue {\r\n        ok = true\r\n        type = ''\r\n        loginType = 'username'\r\n        isShow = true\r\n        list = [1, 2, 3, 1, 2, 3]\r\n\r\n        changeLoginType() {\r\n            this.loginType =\r\n                this.loginType === 'username'\r\n                    ? 'false'\r\n                    : 'username'\r\n        }\r\n\r\n        switchShowState() {\r\n            this.isShow = this.isShow ? false : true\r\n        }\r\n    }\r\n</script>\r\n\r\n<style rel=\"stylesheet/scss\" lang=\"scss\" scoped>\r\n    .borderBold {\r\n        border: 5px solid black;\r\n    }\r\n\r\n    .backgroundBlue {\r\n        background-color: blue;\r\n    }\r\n\r\n    .show-div {\r\n        width: 50px;\r\n        height: 50px;\r\n    }\r\n</style>\r\n\r\n\r\n";