

















































































































import {Vue, Component, Watch} from 'vue-property-decorator'

@Component
export default class BindingHTMLClasses extends Vue {
    ok = true
    type = ''
    loginType = 'username'
    isShow = true
    list = [1, 2, 3, 1, 2, 3]

    changeLoginType() {
        this.loginType =
            this.loginType === 'username'
                ? 'false'
                : 'username'
    }

    switchShowState() {
        this.isShow = this.isShow ? false : true
    }
}
